"use client";
import { useState, useEffect } from "react";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import {
  Tabs,
  TabsContent,
  TabsList,
  TabsTrigger,
} from "@/components/ui/tabs";
import {
  Card,
  CardContent,
} from "@/components/ui/card";
import * as XLSX from "xlsx";

interface LogItem {
  nama: string;
  kelas: string;
  aktivitas: string;
  lokasi: string;
  tanggal: string;
  foto?: string;
}

interface LaporanItem {
  pelapor: string;
  lokasi: string;
  deskripsi: string;
  tanggal: string;
}

interface AgendaItem {
  judul: string;
  tanggal: string;
  keterangan: string;
}

interface GaleriItem {
  lokasi: string;
  jenis: string;
  bentuk: string;
  waktu: string;
  foto?: string;
  video?: string;
}

const getTanggalSekarang = (): string => {
  const hariList = ["Minggu", "Senin", "Selasa", "Rabu", "Kamis", "Jumat", "Sabtu"];
  const bulanList = ["Januari", "Februari", "Maret", "April", "Mei", "Juni", "Juli", "Agustus", "September", "Oktober", "November", "Desember"];
  const now = new Date();
  return `${hariList[now.getDay()]}, ${now.getDate()} ${bulanList[now.getMonth()]} ${now.getFullYear()}`;
};

export default function GercabGo() {
  const [nama, setNama] = useState("");
  const [kelas, setKelas] = useState("");
  const [aktivitas, setAktivitas] = useState("");
  const [lokasi, setLokasi] = useState("");
  const [foto, setFoto] = useState<string | undefined>();
  const [video, setVideo] = useState<string | undefined>();
  const [logList, setLogList] = useState<LogItem[]>([]);
  const [pelapor, setPelapor] = useState("");
  const [laporLokasi, setLaporLokasi] = useState("");
  const [deskripsi, setDeskripsi] = useState("");
  const [laporanList, setLaporanList] = useState<LaporanItem[]>([]);
  const [judulAgenda, setJudulAgenda] = useState("");
  const [tanggalAgenda, setTanggalAgenda] = useState("");
  const [ketAgenda, setKetAgenda] = useState("");
  const [agendaList, setAgendaList] = useState<AgendaItem[]>([]);
  const [jenisBudaya, setJenisBudaya] = useState("");
  const [bentukBudaya, setBentukBudaya] = useState("");
  const [lokasiBudaya, setLokasiBudaya] = useState("");
  const [waktuBudaya, setWaktuBudaya] = useState("");
  const [galeriList, setGaleriList] = useState<GaleriItem[]>([]);

  useEffect(() => {
    const stored = localStorage.getItem("gercab_data");
    if (stored) setLogList(JSON.parse(stored));
    const storedLaporan = localStorage.getItem("gercab_laporan");
    if (storedLaporan) setLaporanList(JSON.parse(storedLaporan));
    const storedAgenda = localStorage.getItem("gercab_agenda");
    if (storedAgenda) setAgendaList(JSON.parse(storedAgenda));
    const storedGaleri = localStorage.getItem("gercab_galeri");
    if (storedGaleri) setGaleriList(JSON.parse(storedGaleri));
  }, []);

  useEffect(() => {
    localStorage.setItem("gercab_data", JSON.stringify(logList));
  }, [logList]);

  useEffect(() => {
    localStorage.setItem("gercab_laporan", JSON.stringify(laporanList));
  }, [laporanList]);

  useEffect(() => {
    localStorage.setItem("gercab_agenda", JSON.stringify(agendaList));
  }, [agendaList]);

  useEffect(() => {
    localStorage.setItem("gercab_galeri", JSON.stringify(galeriList));
  }, [galeriList]);

  const handleLogSubmit = () => {
    if (!nama || !kelas || !aktivitas || !lokasi) return;
    setLogList([...logList, { nama, kelas, aktivitas, lokasi, tanggal: getTanggalSekarang(), foto }]);
    setNama(""); setKelas(""); setAktivitas(""); setLokasi(""); setFoto(undefined);
  };

  const handleFotoChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      const reader = new FileReader();
      reader.onloadend = () => setFoto(reader.result as string);
      reader.readAsDataURL(file);
    }
  };

  const handleVideoChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      const reader = new FileReader();
      reader.onloadend = () => setVideo(reader.result as string);
      reader.readAsDataURL(file);
    }
  };

  const handleLaporanSubmit = () => {
    if (!pelapor || !laporLokasi || !deskripsi) return;
    setLaporanList([...laporanList, { pelapor, lokasi: laporLokasi, deskripsi, tanggal: getTanggalSekarang() }]);
    setPelapor(""); setLaporLokasi(""); setDeskripsi("");
  };

  const handleAgendaSubmit = () => {
    if (!judulAgenda || !tanggalAgenda || !ketAgenda) return;
    setAgendaList([...agendaList, { judul: judulAgenda, tanggal: tanggalAgenda, keterangan: ketAgenda }]);
    setJudulAgenda(""); setTanggalAgenda(""); setKetAgenda("");
  };

  const handleGaleriSubmit = () => {
    if (!lokasiBudaya || !jenisBudaya || !bentukBudaya || !waktuBudaya) return;
    setGaleriList([...galeriList, { lokasi: lokasiBudaya, jenis: jenisBudaya, bentuk: bentukBudaya, waktu: waktuBudaya, foto, video }]);
    setLokasiBudaya(""); setJenisBudaya(""); setBentukBudaya(""); setWaktuBudaya(""); setFoto(undefined); setVideo(undefined);
  };

  const exportToExcel = () => {
    const data = logList.map(({ foto, ...item }) => item);
    const worksheet = XLSX.utils.json_to_sheet(data);
    const workbook = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(workbook, worksheet, "Jejak Hijau");
    XLSX.writeFile(workbook, "jejak_hijau.xlsx");
  };

  return (
    <div className="max-w-4xl mx-auto px-4 pt-8 pb-16 bg-white rounded-3xl shadow-2xl border border-green-300">
      <div className="text-center mb-6">
        <div className="flex items-center justify-center gap-4">
          <img src="/logo.png" alt="Logo" className="w-16 h-16" />
          <div>
            <h1 className="text-3xl font-bold text-green-700">GERCAB-GO</h1>
            <p className="text-sm text-gray-600">Gerakan Cinta Alam dan Budaya</p>
            <p className="text-sm text-gray-500">SMP Negeri 2 Kayen</p>
            <p className="text-xs mt-1 text-gray-400 italic">By: @Mr.Tri25</p>
          </div>
        </div>
      </div>

      <Tabs defaultValue="jejak" className="w-full">
        <TabsList className="grid grid-cols-4 gap-2 bg-green-100 p-2 rounded-xl mb-4">
          <TabsTrigger value="jejak">Jejak Hijau</TabsTrigger>
          <TabsTrigger value="laporan">Lapor</TabsTrigger>
          <TabsTrigger value="galeri">Galeri</TabsTrigger>
          <TabsTrigger value="agenda">Agenda</TabsTrigger>
        </TabsList>

        <TabsContent value="jejak" className="space-y-4">
          <Input placeholder="Nama" value={nama} onChange={(e) => setNama(e.target.value)} />
          <Input placeholder="Kelas" value={kelas} onChange={(e) => setKelas(e.target.value)} />
          <Input placeholder="Aktivitas" value={aktivitas} onChange={(e) => setAktivitas(e.target.value)} />
          <Input placeholder="Lokasi" value={lokasi} onChange={(e) => setLokasi(e.target.value)} />
          <Input type="file" accept="image/*" onChange={handleFotoChange} />
          {foto && <img src={foto} alt="Preview" className="w-40 h-auto border rounded" />}
          <Button onClick={handleLogSubmit} className="bg-green-600 hover:bg-green-700 text-white w-full">Simpan Jejak</Button>
          <Button onClick={exportToExcel} className="bg-yellow-500 hover:bg-yellow-600 text-white w-full">Export Excel</Button>
          <div className="grid gap-2">
            {logList.map((item, idx) => (
              <Card key={idx} className="border border-green-300">
                <CardContent className="p-4">
                  <p><strong>{item.nama}</strong> - {item.kelas}</p>
                  <p className="text-sm text-gray-600">{item.aktivitas} @ {item.lokasi}</p>
                  <p className="text-xs text-gray-400">{item.tanggal}</p>
                  {item.foto && <img src={item.foto} alt="Jejak" className="w-32 mt-2 rounded" />}
                </CardContent>
              </Card>
            ))}
          </div>
        </TabsContent>

        <TabsContent value="laporan" className="space-y-4">
          <Input placeholder="Nama Pelapor" value={pelapor} onChange={(e) => setPelapor(e.target.value)} />
          <Input placeholder="Lokasi" value={laporLokasi} onChange={(e) => setLaporLokasi(e.target.value)} />
          <Input placeholder="Deskripsi Laporan" value={deskripsi} onChange={(e) => setDeskripsi(e.target.value)} />
          <Button onClick={handleLaporanSubmit} className="bg-red-600 hover:bg-red-700 text-white w-full">Kirim Laporan</Button>
          <div className="grid gap-2">
            {laporanList.map((item, idx) => (
              <Card key={idx} className="border border-red-300">
                <CardContent className="p-4">
                  <p><strong>{item.pelapor}</strong> - {item.lokasi}</p>
                  <p className="text-sm text-gray-600">{item.deskripsi}</p>
                  <p className="text-xs text-gray-400">{item.tanggal}</p>
                </CardContent>
              </Card>
            ))}
          </div>
        </TabsContent>

        <TabsContent value="galeri" className="space-y-4">
          <Input placeholder="Lokasi Kegiatan" value={lokasiBudaya} onChange={(e) => setLokasiBudaya(e.target.value)} />
          <Input placeholder="Jenis Kegiatan" value={jenisBudaya} onChange={(e) => setJenisBudaya(e.target.value)} />
          <Input placeholder="Bentuk Budaya" value={bentukBudaya} onChange={(e) => setBentukBudaya(e.target.value)} />
          <Input placeholder="Waktu Kegiatan" value={waktuBudaya} onChange={(e) => setWaktuBudaya(e.target.value)} />
          <Input type="file" accept="image/*" onChange={handleFotoChange} />
          <Input type="file" accept="video/*" onChange={handleVideoChange} />
          {foto && <img src={foto} alt="Preview Foto" className="w-40 h-auto border rounded" />}
          {video && <video controls src={video} className="w-40 h-auto border rounded" />}
          <Button onClick={handleGaleriSubmit} className="bg-green-600 hover:bg-green-700 text-white w-full">Simpan Galeri</Button>
          <div className="grid gap-2">
            {galeriList.map((item, idx) => (
              <Card key={idx} className="border border-green-300">
                <CardContent className="p-4">
                  <p><strong>{item.jenis}</strong> - {item.bentuk}</p>
                  <p className="text-sm text-gray-600">{item.lokasi} - {item.waktu}</p>
                  {item.foto && <img src={item.foto} alt="Foto Budaya" className="w-32 mt-2 rounded" />}
                  {item.video && <video controls src={item.video} className="w-32 mt-2 rounded" />}
                </CardContent>
              </Card>
            ))}
          </div>
        </TabsContent>

        <TabsContent value="agenda" className="space-y-4">
          <Input placeholder="Judul Agenda" value={judulAgenda} onChange={(e) => setJudulAgenda(e.target.value)} />
          <Input placeholder="Tanggal Agenda" value={tanggalAgenda} onChange={(e) => setTanggalAgenda(e.target.value)} />
	      <Input placeholder="Lokasi Agenda" value={tanggalAgenda} onChange={(e) => lokasiAgenda(e.target.value)} />
          <Input placeholder="Keterangan" value={ketAgenda} onChange={(e) => setKetAgenda(e.target.value)} />
          <Button onClick={handleAgendaSubmit} className="bg-blue-600 hover:bg-blue-700 text-white w-full">Simpan Agenda</Button>
          <div className="grid gap-2">
            {agendaList.map((item, idx) => (
              <Card key={idx} className="border border-blue-300">
                <CardContent className="p-4">
                  <p><strong>{item.judul}</strong></p>
                  <p className="text-sm text-gray-600">{item.tanggal}</p>
                  <p className="text-xs text-gray-500">{item.keterangan}</p>
                </CardContent>
              </Card>
            ))}
          </div>
        </TabsContent>
      </Tabs>
    </div>
  );
}
